@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #80deea;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.CartItem {
  margin-top: 3em;
  display: flex;
  justify-content: space-between;
}

.CartItem div {
  width: 25%;
}

.CartItem button {
  width: 30px;
  height: 30px;
  border-radius: 6px;
  background-color: #e1e8ee;
  margin: 0 5px;
  border: none;
}

.CartItem button:disabled {
background-color: #f8f9fa;
}

@media only screen and (min-width: 600px) {
  .CartItem {
    margin-top: 3em;
    display: flex;
    justify-content: space-between;
  }
  
  .CartItem div {
    width: 25%;
  }

}

@media only screen and (max-width: 600px) {
  .CartItem {
    margin-top: 3em;
    display: flex;
    flex-wrap: wrap;  
    
  }
  
  .CartItem div {
    margin-bottom: 1em;
    text-align: center;
    width: 100%;
    font-size: 1.5em;
  }

  
.CartItem button {
  width: 40px;
  height: 40px;
  border-radius: 6px;
  background-color: #e1e8ee;
  margin: 0 10px;
  border: none;
  font-size: 1.5em;
}
}


.Cart {
  width: 750px;
  margin: 80px auto;
  background-color: white;
  border-radius: 7px;
  box-shadow: 1px 2px 3px 0px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  padding: 1em;
}

.Cart-title {
  border-bottom: 1px solid gray;
  color: gray;
  font-size: 1.5 em;
  font-weight: 400;
  padding-bottom: 1em;
}

@media only screen and (min-width: 600px) {
  .Cart {
    width: 70%;
    margin: 80px auto;
    background-color: white;
    border-radius: 7px;
    box-shadow: 1px 2px 3px 0px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    padding: 1em;
  }
}

@media only screen and (max-width: 600px) {
  .Cart {
    width: 70%;
    margin: 40px auto;
    background-color: white;
    border-radius: 7px;
    box-shadow: 1px 2px 3px 0px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 1em;
  }

  .Cart-title {
    border-bottom: 1px solid gray;
    color: gray;
    font-size: 1.5 em;
    font-weight: 400;
    padding-bottom: 1em;
    text-align: center;
  }

  h2.Cart-total {
    margin: 3em auto;
  }
}

